import { Component, h, Prop, Event, EventEmitter } from '@stencil/core';

@Component({
  tag: 'number-input',
  styleUrl: 'number-input.css',
  shadow: true,
})
export class NumberInput {
  @Prop() value: number = 0;
  @Prop() min: number = 0;
  @Prop() max: number = 100;

  @Event() valueChange: EventEmitter<number> | undefined;

  handleInput = (event: Event) => {
    const input = event.target as HTMLInputElement;
    this.valueChange?.emit(Number(input.value));
  };

  render() {
    return (
      <input type="number" value={this.value} min={this.min} max={this.max} onInput={this.handleInput} />
    );
  }
}
